<template>
  <div class="tab-bar-item" @click="itemclick">
   
    <div v-if="!getActive">
      <slot name="icon-item"></slot>
    </div>

    <div v-else>
      <slot name="icon-item-active"></slot>
    </div>

    <div :style="stylecolor">
      <slot name="item-text"></slot>
    </div>
    
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    path: String,
    ActiveColor: {
      type: String,
      default: "red",
    },
  },
  data() {
    return {
      is: false,
    };
  },
  computed: {
    getActive() {
      // 判断路径是否相等 返回布尔值
      // console.log(this.$route.path);
      // console.log('this.path:'+this.path);
      return this.$route.path === this.path; //!this.$route.path.indexOf(this.path)
      
    },
    stylecolor() {
      return this.getActive ? { color: this.ActiveColor } : {};
    },
  },
  components: {},
  mounted() {},
  methods: {
    itemclick() {
      this.$router.push(this.path).catch((err) => err);
    },
  },
};
</script>

<style scoped>
.tab-bar-item {
  flex: 1;
  text-align: center;
  height: 49px;
  font-size: 13px;
}
</style>